.van-index-anchor {
  padding: 0 0.68267rem;
  padding: var(--index-anchor-padding, 0 0.68267rem);
  color: #323233;
  color: var(--index-anchor-text-color, #323233);
  font-weight: 500;
  font-weight: var(--index-anchor-font-weight, 500);
  font-size: 0.59733rem;
  font-size: var(--index-anchor-font-size, 0.59733rem);
  line-height: 1.36533rem;
  line-height: var(--index-anchor-line-height, 1.36533rem);
  background-color: transparent;
  background-color: var(--index-anchor-background-color, transparent);
}
.van-index-anchor--active {
  right: 0;
  left: 0;
  color: #07c160;
  color: var(--index-anchor-active-text-color, #07c160);
  background-color: #fff;
  background-color: var(--index-anchor-active-background-color, #fff);
}
/* stylelint-disable selector-type-no-unknown, selector-max-type */
/**
 * Entry of basic styles
 */
.van-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.van-multi-ellipsis--l2 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-multi-ellipsis--l3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.van-hairline,
.van-hairline--top,
.van-hairline--left,
.van-hairline--right,
.van-hairline--bottom,
.van-hairline--top-bottom,
.van-hairline--surround {
  position: relative;
}
.van-hairline::after,
.van-hairline--top::after,
.van-hairline--left::after,
.van-hairline--right::after,
.van-hairline--bottom::after,
.van-hairline--top-bottom::after,
.van-hairline--surround::after {
  position: absolute;
  box-sizing: border-box;
  -webkit-transform-origin: center;
          transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.van-hairline--top::after {
  /* prettier-ignore */
  border-top-width: 1PX;
}
.van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX;
}
.van-hairline--right::after {
  /* prettier-ignore */
  border-right-width: 1PX;
}
.van-hairline--bottom::after {
  /* prettier-ignore */
  border-bottom-width: 1PX;
}
.van-hairline--top-bottom::after {
  /* prettier-ignore */
  border-width: 1PX 0;
}
.van-hairline--surround::after {
  /* prettier-ignore */
  border-width: 1PX;
}
html,
body,
page {
  font-size: 0.59733rem;
}
